body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
    width: 100vw;
    min-height:100vh;
    background: linear-gradient(to bottom right, #f4a661 20%, #f46036);
}


.badge {
    position: relative;
    margin: 10px;
    padding: 10px;
    min-width: 150px;
    background: #79d856;
    text-align: center;
    font-weight: bold;
    color: #fff;
    text-transform: uppercase;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #fff;
    border-radius: 10px;
}

.badgeTip {
    position: absolute;
    top: -10px;
    right: -10px;
    font-size: 12px;
    background: red;
    padding: 5px;
    border-radius: 50px;
    text-align: center;
    width: 15px;
    height: 15px;
}


.tile {
    display: inline-flex;
    position:relative;
    width:  calc(100%/4);
    max-width: 100px;
    min-width: 50px;
    height: auto;
    justify-content: center;
    align-items: center;
    font-size: 50px;
    color: #2e294e;
    text-align: center;
    background: white;
    border-radius: 20px;
    margin: 2px;
    background: radial-gradient(circle at 50% top, #f8f8f8 60%, #d9d9d9 85%, #cccccc 90%);
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: #2e294e;
}

.diceRow {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width:100%;
}

.tile::before {
    content: '';
    position: absolute;
    display: inline;
    background: transparent;
    width: 85%;
    height: 85%;
    top: 5%;
    border: 1px solid #cccccc;
    border-radius: 100px;
}

.inputWrapper {
    display: inline-flex;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    padding: 10px;
}

.inputWrapper .input {
    font-size: 25px;
    color: #e71d36;
    font-weight: bold;
    text-align: center;
    border: 2px solid #c5d86d;
    border-radius: 50px;
}


.gameBoard {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px 20px;
    margin: 10px;
    background: #00e2d3;
    border-radius: 20px;
    box-shadow: 10px 10px grey;
}

.diceHolder {
    background: rgba(0, 0, 0, .7);
    padding: 5px;
    border-radius: 20px;
    width: 100%;
}

.logo {
    color: #ffffff;
    font-size: 45px;
    font-weight: bolder;
    -webkit-text-stroke-width: 2px;
    width: 100%;
    display: inline-flex;
    justify-content: center;
}

.text-blue {
    color: #2e294e;
    -webkit-text-stroke-color: #2e294e;
}

.text-yellow {
    color: #e71d36;
    -webkit-text-stroke-color: #e71d36;
}

.dice {
    display: inline-flex;
    position: relative;
    border: 1px solid #000;
    padding: 10px;
    width: calc(100% / 10);
    max-width: 50px;
    height: auto;
    margin: 2px;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    background: radial-gradient(circle at 50% top, #f8f8f8 60%, #d9d9d9 85%, #cccccc 90%);

}

.dice::before {
    content: '';
    position: absolute;
    display: inline;
    background: transparent;
    width: 80%;
    height: 80%;
    top: 10%;
    border: 1px solid #cccccc;
    border-radius: 100px;
}

#logoOne,
#logoSix {
    top:10px;
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
}

#logoTwo,
#logoFive {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
}

#logoThree {
    top: 10px;
    -webkit-transform: rotate(160deg);
            transform: rotate(160deg);
}

#logoFour {
    top: 10px;
    -webkit-transform: rotate(30deg);
            transform: rotate(30deg);
}


.listWrapper {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    width: 80%;
    padding: 20px;
}


.buttonWrapper {
    margin: 20px;
    padding: 10px;
}

.buttonWrapper .button {
    padding: 5px 10px;
    font-size: 25px;
    color: #fff;
    font-weight: bolder;
    border: 2px solid #fff;
    border-radius: 5px;
    background: #00e2c8;
}

.buttonWrapper .button:hover {
    cursor: pointer;
}

.summary {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 20px;
}

.summaryBoard {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    padding: 10px 20px;
    margin: 10px;
    width: 100%;
    font-size: 20px;
    color: white;
}

.timer {
    display: flex;
    justify-content: center;
    font-size: 20px;
    color: #fff;
    text-align: center;
}


.headerWrapper {
    padding: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.bodyWrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.textWhite {
    color: #fff;
    font-weight: bold;
    font-size: 20px;
}

a.textLink {
    color: #fff;
    text-decoration: none;
    font-size: 20px;
}

.homePage {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100Vh;
}

